import React, { Component } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import './PlanView.css';
import swal from 'sweetalert2';
import { Card, CardMedia, CardContent, CardActions, Dialog, Typography, Button, CardActionArea, DialogContent } from '@material-ui/core';

const placeholderImage = 'data:image/jpeg;base64,';

const styles = {
  button: {
    margin: 10,
  },
  card: {
    maxWidth: 300,
    minWidth: 250,
    display: 'inline-block',
    height: 340,
    margin: 5,
  },
  media: {
    height: 140,
  },
  cardActionArea: {
    display: 'inline-block',
    width: '100%',
  },
};

class MealCard extends Component {
  state = {
    startDate: null,
    showPicture: true,
    open: false,
    plannedDate: false
  }

  // Date box
  handleDateChange = (event) => {
    // console.log('event',event);
    this.setState({
      startDate: event,
    });
  }

  // Handle Assigning a date to a meal
  handleSubmitDate = () => {
    if (this.state.startDate) {
      this.props.submitDate(this.props.meal.planned_id, this.state.startDate);
      this.setState({ plannedDate: true })

    } else {
      swal(
        'Date?',
        'You must select a date before you can plan the meal.',
        'question'
      )
    }

  }

  // Toggle between picture and direction
  // togglePicture = () => {
  //   this.setState(state => ({ showPicture: !state.showPicture }))
  // }

  // Show directions dialog box
  handleClickPicture = () => {
    this.setState({ open: true });
  }
  handleCloseDialog = () => {
    this.setState({ open: false });
  }

  componentDidMount() {
    if (this.props.meal.planned_day) {
      this.setState({ plannedDate: true })
    }
  }

  // Render 
  render() {
    const { recipe, ...meal } = this.props.meal;

    return (
      <Card style={styles.card}>

        <CardActionArea>
          <CardMedia
            style={styles.media}
            image={recipe.images && recipe.images.length ? `${recipe.images[0].url}?size=500x500` : placeholderImage}
            title="Meal Image"
            onClick={this.handleClickPicture}
          />
          <Dialog
            open={this.state.open}
            onClose={this.handleCloseDialog}
          >
            <DialogContent>
              <ol>
                {recipe.directions.map(direction => <li key={direction.order}>{direction.body}</li>)}
              </ol>
            </DialogContent>
          </Dialog>
        </CardActionArea>

        <CardContent className="cardContent">
          <Typography variant="h5" className="wrapText">
            <div>{recipe.title}</div>
          </Typography>

          <Typography variant="h6">
            {this.state.plannedDate ? <i className="fas fa-check" style={{color: 'blue'}}><span style={{color: 'blue'}}> Meal planned!</span></i> : null}
          </Typography>

        </CardContent>

        <CardActions>
          <DatePicker
            onChange={this.handleDateChange}
            selected={this.state.startDate}
            placeholderText="Click to assign date"
          />
        </CardActions>
        <CardActions>
          <Button
            variant="contained"
            color="primary"
            styles={styles.button}
            onClick={this.handleSubmitDate}
          >
            Plan
          </Button>
          <Button
            variant="contained"
            // color="secondary"
            styles={styles.button}
            onClick={this.props.removeMeal(meal.planned_id)}
          >
            Remove
          </Button>
        </CardActions>
      </Card>

    );
  }
}


export default MealCard;

